/* ==========================================================================
   1. Common css start
   ========================================================================== */

@import url(' https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800&display=swap');


body {
    font-family: $open_sans;
    font-weight: 400;
    font-style: normal;
    line-height: 1.2;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


img {
    vertical-align: middle;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
    text-decoration: none;
    outline: none;
}

a,
a:focus,
a:hover {
    text-decoration: none;
}

i,
span,
a {
    display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
    margin: 0px;
}

h1 {
    font-weight: 500;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 28px;
}

h4 {
    font-size: 22px;
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 16px;
}

ul,
ol {
    margin: 0px;
    padding: 0px;
    list-style-type: none;
}

.fluid-img {
    max-width: 100% !important;
}

.text-center {
    text-align: center;
}

p {
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
    margin: 0;
    padding: 0;

    @include media-breakpoint-up(md) {
        font-size: 16px;
    }

}


.back-to-top {
    width: 40px;
    line-height: 40px;
    overflow: hidden;
    z-index: 9;
    display: none;
    cursor: pointer;
    position: fixed;
    bottom: 50px;
    right: 0;
    background-color: rgb(10, 60, 102);
    color: #fff;
    text-align: center;
    font-size: 20px;
    transition: all ease-out 0.2s;

    &:hover {
        color: #000;
    }
}

/* ==========================================================================
   Common css end
   ========================================================================== */