/* ==========================================================================
   3. Hero css start
   ========================================================================== */




.hero {
    background: url(../images/hero.png) no-repeat center;
    background-size: cover;
    height: 100vh;
    position: relative;
    z-index: 1;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #000;
        opacity: 0.7;
        z-index: -1;

    }

    p {
        padding: 100px 0;
        text-align: center;
        color: #fff;
    }
}





/* ==========================================================================
   3. Hero css end
   ========================================================================== */