.sticky {
  background-image: linear-gradient(109.6deg, rgba(9, 154, 151, 1) 11.2%, rgba(21, 205, 168, 1) 91.1%);
  -moz-box-shadow: 0 2px 5px rgba(16, 16, 16, .07);
  -webkit-box-shadow: 0 2px 5px rgba(16, 16, 16, .07);
  box-shadow: 0 2px 5px rgba(16, 16, 16, .07);
  width: 100%;
  padding: 0;
  @include animation(sticky 1s);
}

header {
  padding: 10px 0;
  position: fixed;
  transition: all ease-in-out 0.5s;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  background: rgb(8, 122, 103);


  a {
    transition: all ease-in-out 0.3s;
    color: $white;
    padding: 0 10px;

    &:last-child {
      padding-right: 0;
    }

    &:hover {
      color: $black !important;
    }
  }
  .active{
    color: #000!important;
  }


}

@include media-breakpoint-up(lg) {


  @keyframes sticky {
    0% {
      -webkit-transform: translateY(-100%);
      transform: translateY(-100%)
    }

    100% {
      -webkit-transform: translateY(0%);
      transform: translateY(0%)
    }
  }
}

@include media-breakpoint-down(md) {

  *,
  *::after,
  *::before {
    box-sizing: border-box;
  }



  .nav-open main {
    transform: scale(.8);
  }

  .nav-trigger {
    position: fixed;
    z-index: 4;
    right: 15px;
    height: 44px;
    width: 44px;

    overflow: hidden;
    color: transparent;
    white-space: nowrap;
    text-indent: 100%;
  }

  .nav-trigger span,
  .nav-trigger span::before,
  .nav-trigger span::after {
    position: absolute;
    height: 2px;
    width: 30px;
    background: #999999;
  }

  .nav-trigger span {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: background .3s;
  }

  .nav-trigger span::before,
  .nav-trigger span::after {
    content: '';
    top: 0;
    left: 0;
    transition: background .3s, transform .3s;
  }

  .nav-trigger span::before {
    transform: translateY(-10px);
  }

  .nav-trigger span::after {
    transform: translateY(10px);
  }

  .nav-trigger:hover span,
  .nav-trigger:hover span::before,
  .nav-trigger:hover span::after {
    background: white;
  }

  .nav-open .nav-trigger span {
    background: transparent;
  }

  .nav-open .nav-trigger span::before,
  .nav-open .nav-trigger span::after {
    background: #1F2B3D;
  }

  .nav-open .nav-trigger span::before {
    transform: rotate(-45deg);
  }

  .nav-open .nav-trigger span::after {
    transform: rotate(45deg);
  }

  .overlay {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #1C1D21;
    opacity: 0;
    visibility: hidden;
    transition: opacity .5s, visibility .5s;
  }

  .nav-open .overlay {
    opacity: .6;
    visibility: visible;
  }

  .nav-container {
    position: fixed;
    z-index: 3;
    top: 0;
    right: 0;
    height: 100%;
    width: 80%;
    max-width: 460px;
    padding: 4em 3.5em;
    background: #fff;
    overflow: auto;
    transform: translateZ(0);
    transform: translateX(100%);
    transition: transform .5s cubic-bezier(.07, .23, .34, 1);
  }

  .nav-open .nav-container {
    transform: translateX(0);
  }

  .nav-container h2 {
    font-size: 15px;
    font-weight: bold;
    text-transform: uppercase;
    color: #AAAAAA;
    margin: 1.5em 0;
  }

  .nav {
    list-style: none;
    padding: 0;
  }

  .nav a {
    display: block;
    padding: .4em 0;
    font-size: 24px;
    font-weight: 400;
    font-family: $open_sans;
    text-decoration: none;
    color: #DF0E62;
    transform: translateZ(0);

  }

  .nav-open .nav a {
    animation: slide-in .4s .2s backwards;
  }

  .nav-open .nav li:nth-of-type(2) a {
    animation-delay: .3s;
  }

  .nav-open .nav li:nth-of-type(3) a {
    animation-delay: .4s;
  }

  .nav-open .nav li:nth-of-type(4) a {
    animation-delay: .5s;
  }

  .nav-open .nav li:nth-of-type(5) a {
    animation-delay: .6s;
  }

  @keyframes slide-in {
    0% {
      opacity: 0;
      transform: translateX(80px);
    }

    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }


}

.social-media {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);

  a {
    padding: 0 7px;
    color: #000;
  }
}